import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      redirect: "/textflint",
      component: () => import("@/view/layout/Layout"),
      children: [
        {
          path: "/textflint",
          name: "textflint",
          component: () => import("@/view/pages/Textflint.vue")
        },
        {
          path: "/chartdisplay",
          name: "chartdisplay",
          component: () => import("@/view/pages/ChartDisplay.vue")
        },
        {
          path: "/builder",
          name: "builder",
          props: true,
          component: () => import("@/view/pages/Builder.vue")
        }
      ]
    },
    {
      path: "*",
      redirect: "/404"
    },
    {
      // the 404 route, when none of the above matches
      path: "/404",
      name: "404",
      component: () => import("@/view/pages/error/Error-1.vue")
    }
  ]
});

